body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
}

body {
  background-color: #7cc6fe;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* ToolBar */
.toolbar {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 10vh;
  background: #2f3640;
  -webkit-box-shadow: 0px 10px 15px 5px rgba(116, 116, 116, 0.73);
  box-shadow: 0px 10px 15px 5px rgba(116, 116, 116, 0.73);
  display: flex;
  align-items: center;
  justify-content: center;
}

.toolbar h1 {
  font-size: 25;
  color: rgba(0, 0, 0, 0.6);
  text-shadow: 3px 2px 3px rgba(255, 255, 255, 0.2);
}

/* questions */

.questions {
  width: 96vw;
  height: 70vh;
  position: fixed;
  left: 1vw;
  top: 15vh;
  border-radius: 10px;
  background-color: #252d4a;
  padding: 1vw;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
}

.question-title {
  font-size: 20px;
  width: 80vw;
  margin: 0 auto;
  text-align: center;
  line-height: 35px;
}

.answer-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
}

.answer-box-grid {
  width: 100%;
  /* height: 100%; */
  display: grid;
  grid-gap: 30px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-auto-flow: row;
}

.answer-box {
  width: 80%;
  height: 50%;
  padding: 20px;
  margin: 0 auto;
  display: flex;
  text-align: center;
  align-items: center;
  border-radius: 15px;
  cursor: pointer;
  background: #6e6e6e;
}

.answer-box:hover {
  background-color: #7cc6fe;
}

.answer-box p {
  font-size: 18px;
  color: rgba(0, 0, 0, 0.6);
  text-shadow: 3px 2px 3px rgba(255, 255, 255, 0.2);
}

.quiz-input {
  color: black;
}

.answer-box-correct {
  background: green !important;
}

.answer-box-wrong {
  background: red !important;
}

.next-question-btn {
  width: 10vw;
  padding: 20px;
  position: fixed;
  top: 45%;
  right: 0px;
  background: #0e1016;
  border-radius: 10px;
  cursor: pointer;
  text-align: end;

  transition: 1.3s;
}
.next-question-btn:hover {
  background: #373e55;
}

/* start-quiz-container */

.start-quiz-container {
  width: 100%;
  background: #7cc6fe;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
.start-quiz-container h1 {
  width: 50%;
  text-align: center;
  margin-top: -10vh;
  margin-bottom: 10vh;
  color: #2f3640;
}
.start-quiz-container input {
  padding: 10px;
  text-align: center;
  width: 10vw;
  height: 10vw;
  font-size: 35px;
  border-radius: 50%;
  background: #84888fb2;
  outline: none;
}
.start-quiz-container button {
  padding: 20px;
  width: 20vw;
  background: #2f3640;
  cursor: pointer;
  border-radius: 10px;
  font-size: 20px;
}

.start-quiz-container > * {
  margin-bottom: 10px;
}
